:root {
  --dark-purple: #2c0735;
  --dark-blue: #1e1e40;
  --medium-purple: #5c3f6e;
  --light-blue: #a3a3c2;
  --white: #ffffff;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--dark-purple);
  color: var(--light-blue);
}

.ImageData {
  text-align: center;
  padding: 40px 20px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--white);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  margin-bottom: 50px;
}

.gallery-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  max-width: 1200px;
  width: 100%;
}

.gallery-item {
  background-color: var(--dark-blue);
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.gallery-item:hover {
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.5);
}

.gallery-item img {
  width: 100%;
  max-width: 400px;
  height: auto;
  aspect-ratio: 1 / 1; 
  object-fit: cover; 
  border-radius: 10px;
  display: block;
  margin-bottom: 15px;
}

.gallery-item p {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 500;
  color: var(--light-blue);
}

@media (max-width: 768px) {
  h1 {
    font-size: 2rem;
    margin-bottom: 30px;
  }

  .ImageData {
    padding: 20px 10px;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 1.8rem;
  }

  .gallery-container {
    grid-template-columns: 1fr;
  }
}